@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;700;800&display=swap);
html{scroll-behavior:smooth}body{background-color:#232323;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white;scrollbar-width:none}body::-webkit-scrollbar{width:10px}@media (max-width: 768px){body::-webkit-scrollbar{display:none}}body::-webkit-scrollbar-track{background-color:#e4e4e400;border-radius:100px}body::-webkit-scrollbar-thumb{background-color:#000000;border-radius:100px}.TitleDiv{margin-left:100px;padding-top:150px}@media (max-width: 768px){.TitleDiv{margin-left:25px;padding-top:250px}}.title1{color:#888888;font-size:100px;font-weight:800;margin:0;line-height:60%}@media (max-width: 768px){.title1{font-size:80px}}.title2{color:white;font-size:120px;font-weight:800;margin:0;line-height:80%;margin-top:20px}@media (max-width: 768px){.title2{font-size:80px}}.title3{color:#888888;font-size:50px;font-weight:200;margin:0;line-height:80%;margin-top:20px}@media (max-width: 768px){.title3{font-size:30px}}.subheading{color:#F26B4E;font-weight:800;text-align:center;font-size:40px}@media (max-width: 768px){.subheading{font-size:30px}}.desc{margin:40px 20%;text-align:center}.NavBar{height:50px;width:40%;padding:0 30%;position:fixed;background-color:#232323;z-index:2}@media (max-width: 768px){.NavBar{padding:0 0%;width:100%}}.NavBarList{height:100%;display:flex;flex-direction:row;justify-content:space-around;align-items:center}@media (max-width: 768px){.NavBarList{justify-content:space-around;padding:0;width:90%}}.NavBarList a{color:white;width:5%;font-weight:500;font-size:small;text-decoration:none}.NavBarListItem:hover{color:#F26B4E}.SectionDiv{margin-top:150px;height:100vh;display:flex;align-items:center;flex-direction:column;scroll-snap-align:start;justify-content:center}.PortfolioDiv{padding-top:50px}.App{-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory}.ContactInnerDiv a{margin:0 10px}


.item{display:flex;align-items:center;justify-content:center;color:white;position:relative;transition:all .5s ease;background-color:black}.ImgItem{width:100%;height:100%;object-fit:cover;z-index:1}img{transition:all .5s ease}.newImg:hover{opacity:0%;z-index:0}.ImgTitle{position:absolute;pointer-events:none}@media (max-width: 768px){.ImgTitle{font-size:small}}.newImg{width:100%;height:100%}.grid{margin:40px 0}

.ModalBackground{width:100%;height:100%;background:rgba(0,0,0,0.829);position:fixed;display:flex;justify-content:center;align-items:center;z-index:1;top:0%;left:0%}.ModalWrapper{width:40%;box-shadow:0 5px 16px rgba(0,0,0,0.2);background:#704141;color:#000;display:flex;position:relative;z-index:10;border-radius:10px}@media (max-width: 768px){.ModalWrapper{width:80%}}.ModalImg{width:100%}

